<template>
  <ScrollTop></ScrollTop>
  <div class="container">
    <CardPv class="bc" :title="project.name" :brief="project.brief" >
      <CarouselBs :cn="id" :imgs-path="project.imgs" :isPreview="true"></CarouselBs>
    </CardPv>
    <description-comp :project-number="project.id" class="description"></description-comp>
  </div>
  <show-case :project-number="project.id"></show-case>

</template>



<!--<Image-->
<!--    :src="x"-->
<!--    preview-->
<!--    class="rounded-lg shadow cursor-pointer carousel-img d-block w-100"-->
<!--    :alt="index"-->
<!--/>-->


<script>
import { mapGetters } from "vuex";
import CarouselBs from "@/components/CarouselBs.vue";
import DescriptionComp from "@/components/DescriptionComp.vue";
import ShowCase from "@/components/ShowCase.vue";
import CardPv from "@/components/CardPv.vue";
import ScrollTop from 'primevue/scrolltop';

export default {
  components: {CardPv, CarouselBs, DescriptionComp, ShowCase, ScrollTop},
  data(){
    return {
      id: null,
      project: null
    }
  },
  computed: {
    ...mapGetters({
      'projects': 'getprojects'
    })
  },
  created(){
    this.id = Number(this.$route.params.id)
    for (let i = 0; i < this.projects.length; i++) {
      if (this.projects[i].id === this.id) {
        this.project = this.projects[i];
        break;
      }
    }
    console.log(typeof this.project);

  },
  name: "ProjectDetails"
}
</script>


<style scoped>
.container {
  display: flex;
  flex-wrap: wrap;       /* allows children to wrap instead of squish */
}

.bc,
.description {
  flex: 1 1 100%;        /* default: full width (mobile safe) */
  max-width: 100%;
}

/* On medium/large screens, split 50/50 */
@media (min-width: 768px) {
  .bc,
  .description {
    flex: 1 1 50%;
    max-width: 50%;
  }
}

.description {
  text-align: left;
  padding: 2rem;         /* reduce padding on smaller screens */
}

@media (min-width: 768px) {
  .description {
    padding: 5rem;
  }
}

</style>


