import { createRouter, createWebHashHistory } from 'vue-router'
// import AboutMe from "@/components/AboutMe.vue";
import HomeComp from "@/components/HomeComp.vue";
// import ContactMe from "@/components/ContactMe.vue";
import ServiceComp from "@/components/ServiceComp.vue";
import ProjectsComp from "@/components/ProjectsComp.vue";
import ProjectDetails from "@/components/ProjectDetails.vue";

const routes = [
    { path: '/', name: 'Home_1', component: HomeComp },
    { path: '/', name: 'Home_2', component: HomeComp },
    // { path: '/about', name: 'About', component: AboutMe },
    // { path: '/contact', name: 'Contact', component: ContactMe },
    { path: '/services', name: 'Services', component: ServiceComp },
    { path: '/projects', name: 'Projects', component: ProjectsComp },
    { path: '/projects/:id', name: 'ProjectsDetails', component: ProjectDetails, props: true },

]

const router = createRouter({
    // history: createWebHistory(),
    history: createWebHashHistory(),
    routes,
    scrollBehavior(to, from, savedPosition) {
        if(savedPosition) {
            // if navigating with back/forward buttons  restore position
            return savedPosition
        } else {
            // if navigating normally  scroll to top
            return { left: 0, top: 0 }
        }
    }
})

export default router