<template>
  <ScrollTop></ScrollTop>

  <div class="grid-container">
    <CardPv v-for="project in projects" :key="project" :title="project.name" :brief="project.brief">
      <RouterLink :to="{ name: 'ProjectsDetails', params: { id: project.id } }">
        <CarouselBs :cn="project.id" :imgs-path="project.imgs" :isPreview="false"></CarouselBs>
      </RouterLink>
    </CardPv>
  </div>

</template>


<script>
import CarouselBs from "@/components/CarouselBs.vue";
import CardPv from "@/components/CardPv.vue";
import { mapGetters } from "vuex";
import ScrollTop from "primevue/scrolltop";

export default {
  name: "ProjectsComp",
  components: {CarouselBs, CardPv, ScrollTop},
  computed: {
    ...mapGetters({
      'imgs': 'getimgs',
      'projects': 'getprojects',
    })
  }
}
</script>



<style>
.grid-container {
  display: grid;
  /* min() caps the track min-width to the container width on small screens */
  grid-template-columns: repeat(auto-fit, minmax(min(100%, 550px), 1fr));
  gap: 10px;
}
</style>