<template>
  <div  :id="`carousel-${cn}`" class="carousel slide carousel-outer" data-bs-ride="carousel" style="max-width: 700px; max-height: 500px">
    <div class="carousel-inner">
<!--      <div v-if="imgsPath"  class="carousel-item active">-->
<!--        <img class="carousel-img d-block w-100" :src="imgsPath[0]" alt="0" />-->
<!--      </div>-->
<!--      <div v-if="imgsPath.length > 1" v-for="(x, index) in imgsPath.slice(1)" :key="x" class="carousel-item">-->
<!--        <img class="carousel-img d-block w-100" :src="x" :alt="index" />-->
<!--      </div>-->


      <div v-for="(x, index) in imgsPath" :class="{'active': index === 0, 'carousel-item': true}" :key="x">
        <img v-if="!isPreview" class="carousel-img d-block w-100" :src="x" :alt="index" />
        <Image
            v-else
            :src="x"
